import React from 'react';
import { Link } from 'react-router-dom';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink } from 'reactstrap';

 import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export class Navigation extends React.Component{
	ToggleSlider(e){
		e.preventDefault();
		const SideNavigation = document.getElementsByClassName('SideNavigation')[0];
			  SideNavigation.classList.toggle('SideNavigationToggle')
			console.log(SideNavigation);
	}
	render(){
		return(
			<div className="Navigation">
		        <Navbar light expand="md">
		          <NavbarBrand href="/" onClick={(e) => (this.ToggleSlider(e))}>
		          	<FontAwesomeIcon icon="bars" />
		          </NavbarBrand>
		       	</Navbar>
		    </div>
		)
	}
}
