import React from 'react';
import { Link } from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export class SideNavigation extends React.Component{
	ToggleSlider(e){
		e.preventDefault();
		const SideNavigation = document.getElementsByClassName('SideNavigation')[0];
			  SideNavigation.classList.toggle('SideNavigationToggle')
			console.log(SideNavigation);
	}
	render(){
		return(
			<div className="SideNavigation">
				<div className="Menu-SideNavigation">
					<div className='logo'>
						<FontAwesomeIcon icon="times" onClick={(e) => (this.ToggleSlider(e))}/>
					</div>
					<ul>
						<li>
							<Link to='/'> Home Page </Link>
						</li>
						<li>
							<Link to='/project'> Project </Link>
						</li>
						<li>
							<Link to='/contact'> Contact </Link>
						</li>
					</ul>
				</div>
			</div>
		)
	}
}