import React from 'react';
import { Jumbotron, Container, Row, Col, Card, CardBody, CardTitle, CardText, Button } from 'reactstrap';


export class FirstPage extends React.Component{
	state = {
		dateNow : null
	}

	componentDidMount(){
		setInterval(()=>{
			this.setState({
				dateNow: new Date().toLocaleTimeString()
			})
		}, 1000)
	}
	

	render(){
		const {dateNow} = this.state;
		return(
			<div className='FirstPage'>
				<Jumbotron fluid>
					<Container fluid>
						<Row>
							<Col lg='12'>	
								<Card className='mb-3'>
									<CardBody>
										<CardText> Sandy Kurniawan </CardText>
									</CardBody>
								</Card>
							</Col>
						</Row>
						<Row>
							<Col lg='4' className='mx-auto'>
								<Card className='mb-3'>
									<CardBody>
										<CardText>
											{dateNow}
										</CardText>
									</CardBody>
								</Card>
							</Col>
						</Row>
						<Row>
							<Col lg='9' className='mx-auto'>
								<Card className='mb-3'>
									<CardBody>
										<hr data-content="BlackBox" className='hr-text' />
									</CardBody>
								</Card>
							</Col>
						</Row>
					</Container>
				</Jumbotron>
			</div>
		)
	}
}